	module.exports = {"data":{"markdownRemark":{"html":"<h2>Quantitative Testing</h2>\n<p>This is a stub. <a href='https://github.com/freecodecamp/guides/tree/master/src/pages/product-design/quantitative-testing/index.md' target='_blank' rel='nofollow'>Help our community expand it</a>.</p>\n<p><a href='https://github.com/freecodecamp/guides/blob/master/README.md' target='_blank' rel='nofollow'>This quick style guide will help ensure your pull request gets accepted</a>.</p>\n<p>Quantitative Testing means testing in order to establish the frequency, magnitude, recurrence of a given hypothesis or phenomenon.</p>\n<p>In software terms it could many any of the following (but not limited to):</p>\n<ul>\n<li>Percentage of unique users taking a certain action on your website/interface</li>\n<li>Number of times your app crashes</li>\n<li>Mean response time of your server for a specific request by a set number of users</li>\n<li>Total number of requests your server can handle before the response time begins to degrade</li>\n</ul>\n<p>As such Quantitative Testing could be performed within any of the following umbrella testing activites:</p>\n<h3>A/B Testing</h3>\n<h3>Performance Testing</h3>\n<h3>Load Testing</h3>\n<h4>More Information:</h4>\n<!-- Please add any articles you think might be helpful to read before writing the article -->","fields":{"slug":"/product-design/quantitative-testing/"},"frontmatter":{"title":"Quantitative Testing"}}},"pathContext":{"slug":"/product-design/quantitative-testing/"}}

